textarea {
  width: 300px;
  height: 100px;
}

pre {
  white-space: pre-wrap;
}

.d-flex-vertical {
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.segment {
  font-size: 30px;
  font-family: serif;
  display: inline;
  
  .pinyin, .english {
    font-size: 10px;
    max-width: 10em;
    max-height: 30px;
    overflow: scroll;
    overflow-wrap: break-word;
    word-wrap: break-word;
    hyphens: auto;
  }
}